/* styling for the clear button */
.delete_btn {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  font-size: 1.4em;
  background-color: #c5c3cd;
  color: #000000;
  flex: 1;
  outline: 1px solid #000000;
  border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px;
}
